@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@400;700&display=swap);
* {
  box-sizing: border-box;
  font-family: "Montserrat", sans-serif;
  margin: 0;
  padding: 0;
}

body {
  margin: 0;
  background-color: #000;
}

.Navbaritems {
  background: linear-gradient(
    90deg,
    rgba(2, 0, 36, 1) 0%,
    rgba(102, 50, 170, 1) 0%,
    rgba(80, 34, 208, 1) 100%
  );

  height: 80px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.2rem;
  width: 100%;
}
.Navbar-logo {
  color: #fff;
  justify-self: start;
  margin: 20px;
  cursor: pointer;
  position: absolute;
  top: 0;
  left: 0;
}

.navmenu {
  display: grid;
  grid-template-columns: repeat(5, auto);
  grid-gap: 10px;
  justify-content: end;
  margin: 20px;
  list-style: none;
  position: absolute;
  top: 0;
  right: 0;
}

.nav-link {
  color: #fff;
  text-decoration: none;
  padding: 10px;
}

.nav-link:hover {
  background-color: rgb(128, 66, 243);
  border-radius: 10px;
}

.menu-icon {
  cursor: pointer;
}

.nav-link-mobil {
  display: none;
}

.menu-icon {
  display: none;
}

@media screen and (max-width: 960px) {
  .Navbaritems {
    position: relative;
  }
  .hidden {
    display: none;
  }
  .navmenu {
    display: flex;
    flex-direction: column;
    height: 220px;
    text-align: center;
    opacity: 1;
    transition: all 0.1s ease;
    position: absolute;
    top: 60px;
    left: -1000%;
  }
  .navmenu.active {
    background-color: rgb(126, 81, 208);
    width: 100%;
    height: 260px;
    margin-left: 0;
    position: absolute;
    top: 60px;
    left: 0%;
    transition: all 0.5s ease;
  }
  .navmenu.active .nav-link {
    display: table;
    text-align: center;
    width: 100%;
  }
  .menu-icon {
    display: block;
    position: absolute;
    top: 0;
    right: 0;
    margin: 10px;
    font-size: 2.4rem;
  }
  .nav-link:hover {
    background-color: rgb(128, 66, 243);
    border-radius: 0px;
  }
}

.container-slider {
  /* display: none; */
  width: 100%;
  height: 500px;
  margin: 100px auto 0;
  position: relative;
  overflow: hidden;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.2);
}
.h {
  width: 100%;
  height: 500px;
  position: absolute;
  opacity: 0;
  transition: opacity ease-in-out 1s;
}
@media screen and (max-width: 700px) {
  .container-slider {
    margin: 0;
  }
}
.slide {
  width: 100%;
  height: 100%;
  position: absolute;
  opacity: 0;
  transition: opacity ease-in-out 0.4s;
}
.slide img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.active-anim {
  opacity: 1;
}

.btn-slide {
  width: 60px;
  height: 60px;
  border-radius: 50%;
  background: #f1f1f1;
  border: 1px solid rgba(34, 34, 34, 0.287);
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}

.btn-slide img {
  width: 25px;
  height: 25px;
  pointer-events: none;
}
.prev {
  top: 50%;
  left: 20px;
  -webkit-transform: translateY(-60%);
          transform: translateY(-60%);
}
.next {
  top: 50%;
  right: 20px;
  -webkit-transform: translateY(-60%);
          transform: translateY(-60%);
}

.container-dots {
  position: absolute;
  bottom: 10px;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  display: flex;
}
.dot {
  width: 20px;
  height: 20px;
  border-radius: 50%;
  border: 3px solid #f1f1f1;
  margin: 0 5px;
  background: #f1f1f1;
}
.dot.active {
  background: rgb(32, 32, 32);
}


